
html{box-sizing:border-box
}
body{margin:0;background-color:#333439;min-width:500px
}
@font-face{font-family:'ratio';src:url("../assets/fonts/ratio.woff2") format("woff2"),url("../assets/fonts/ratio.woff") format("woff"),url("../assets/fonts/ratio.ttf") format("truetype");font-weight:normal;font-style:normal
}
.yellow{color:#facb16
}
a.yellow:hover{color:#FFE489
}
#app{font-family:"ratio",Helvetica,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#d3d3d3;margin:0;letter-spacing:0.15em;position:relative;padding-bottom:6vh;min-height:97vh
}
.parallax{background-image:url("../assets/welcomeBackground.png");background-attachment:fixed;background-repeat:no-repeat
}
.live{display:inline-block;background-color:#af0000;padding:5px 10px;border-radius:10px;white-space:nowrap
}
.svg-glyph_live{fill:#d3d3d3;vertical-align:top
}
p{color:#d3d3d3;margin:5px
}
a{text-decoration:none;color:#d3d3d3;margin:0
}
ul{cursor:default
}
h1,h2,h3{font-weight:lighter
}
h2,h3{color:#facb16;font-size:26px;margin:0
}
h3{font-size:16px;margin:5px 0
}



